// next-app/pages/login.js
import { useState } from 'react';
import axios from '../services/api';
import { useRouter } from 'next/router';
import Link from 'next/link'; // <--- ADD THIS LINE

export default function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(''); // State to hold error messages
  const router = useRouter();

  const handleLogin = async () => {
    setError(''); // Clear previous errors
    try {
      const res = await axios.post('/auth/login', { email, password });
      console.log('Login Success:', res.data);

      // Store token and user data received from the backend
      localStorage.setItem('token', res.data.token);
      localStorage.setItem('user', JSON.stringify(res.data.user)); // Store the user object

      router.push('/'); // Redirect to the homepage after successful login
    } catch (err) {
      console.error('Login Failed:', err.response?.data || err.message);
      setError(err.response?.data?.error || 'Login failed. Please check your credentials.'); // Display specific error or generic
    }
  };

  return (
    <div className="p-4 max-w-md mx-auto">
      <h2 className="text-xl font-bold">Login</h2>
      {error && <p className="text-red-500 mb-4">{error}</p>} {/* Display error message */}
      <input className="border p-2 w-full" value={email} onChange={e => setEmail(e.target.value)} placeholder="Email" />
      <input className="border p-2 w-full mt-2" type="password" value={password} onChange={e => setPassword(e.target.value)} placeholder="Password" />
      <button className="bg-blue-600 text-white p-2 mt-4 w-full" onClick={handleLogin}>Login</button>

      <p className="mt-4 text-center">
        Don't have an account?{' '}
        <Link href="/register" className="text-blue-500 hover:underline">
          Register
        </Link>
      </p>
    </div>
  );
}